import { PatientContext } from '../Model/PatientContext';
import { useContext } from 'react';
import { format, } from 'date-fns';

export default function HeaderPatient() {

    const { selectedPatient } = useContext(PatientContext);


    return (
        <div style={{ width: '30%', background: 'white', textAlign: 'center', borderRadius: 15, alignItems: 'center', display: 'flex', flexDirection: 'column', justifyContent: 'center' }}>
            <div >
                <h5>{selectedPatient.name} {selectedPatient.surname}  {format(selectedPatient.birthdate, 'dd-MM-y')}</h5>
            </div>
        </div>
    )
}