import PatientModel from "../Model/PatientModel"
import { useState } from "react"
import male from '../img/male.png'
import question from '../img/icon/question.png'
import Slider from '@mui/material/Slider';
import { DayPicker } from "react-day-picker";
import "react-day-picker/dist/style.css";
import { format, } from 'date-fns';
import { Select, MenuItem, FormControl, InputLabel } from "@mui/material";
import { Checkbox, Alert, AlertTitle } from "@mui/material";
import { useNavigate } from "react-router-dom";
import NewPatientModal from "./NewPatientModal";


export default function NewPatient() {

    const marksH = [{ value: 180, label: '180cm', }, { value: 170, label: '170cm', }, { value: 190, label: '190cm', }, { value: 160, label: '160cm', }, { value: 220, label: '220cm', }, { value: 120, label: '120cm', }];
    const marksW = [{ value: 40, label: '40kg', }, { value: 50, label: '50kg', }, { value: 60, label: '60kg', }, { value: 70, label: '70kg', }, { value: 80, label: '80kg', }, { value: 90, label: '90kg', }, { value: 100, label: '100kg', }, { value: 150, label: '150kg', }];
    const [patient, setPatient] = useState(new PatientModel('Mario', 'Rossi', '', 'M', 170, 75, []))
    const [showDatePicker, setShowDatePicker] = useState('none')
    const [dateButtonClass, setDateButtonClass] = useState("btn btn-primary btn-lg")
    const [protButtonClass, setProtButtonClass] = useState("btn btn-warning")
    const [showProthesis, setShowProthesis] = useState('none')
    const [showModal, setShowModal] = useState(false)
    const [disabled, setDisabled] = useState(false)
    const [sendingButton, setSendingButton] = useState('Conferma e procedi')
    const [showAlert, setShowAlert] = useState(false)
    const navigate = useNavigate();

    const handleNew = async () => {
        setDisabled(true)
        setSendingButton("Inviando...")
        setTimeout(() => {
            patient.register()
            setShowAlert(true)
        }, 3000)
    }

    const modifyPatientName = (event) => {
        let p = patient.clone()
        if (event.target.id === 'name') {
            p.setName(event.target.value)
        } else {
            p.setSurname(event.target.value)
        }
        setPatient(p);
    }

    const modifyPatientDimensions = (event, dimension) => {
        let p = patient.clone()
        if (event.target.name === 'weight') {
            p.setWeight(dimension)
        } else {
            p.setHeight(dimension)
        }
        setPatient(p);
    }

    const modifyPatientBirthdate = (date) => {
        console.log('modifico data nascita')
        let p = patient.clone()
        p.setBirthdate(format(date, 'y-MM-dd'))
        setPatient(p)
        setShowDatePicker('none')
        setDateButtonClass("btn btn-outline-primary btn-lg")
    }

    const handleGender = (event) => {
        let p = patient.clone()
        p.setGender(event.target.value)
        console.log(p.toString())
        setPatient(p)
    }

    const handleProthesis = (event) => {
        let p = patient.clone()
        if (event.target.checked) {
            p.addProthesis(event.target.name)
        } else {
            p.deleteProthesis(event.target.name)
        }
        setPatient(p)
    }

    const handleDisplayProthesis = () => {
        console.log('handleDisplayProthesis al momento mostro %s', showProthesis)
        if (showProthesis === 'none') {
            setShowProthesis('flex')
            setProtButtonClass("btn btn-outline-warning")
        } else {
            setShowProthesis('none')
            setProtButtonClass("btn btn-warning")
        }
    }

    return (
        <div className="box-bianco" style={style.box}>
            <div className="input-group mb-3" style={{
                justifyContent: 'center',
                display: 'flex',
                margin: 'auto',
                alignItems: 'center', padding: 20
            }}>
                <input id='name' type="text" placeholder="nome..." onChange={e => modifyPatientName(e)} />
                <input id='surname' type="text" placeholder="cognome..." onChange={e => modifyPatientName(e)} />
            </div>
            <div className="fascia centrale" style={{
                display: 'flex',
                flexDirection: 'row',
                justifyContent: 'center',
                textAlign: 'center'
            }}>
                <div style={style.leftButtons}>
                    <div onMouseLeave={() => setShowDatePicker('none')}>
                        <button onClick={() => setShowDatePicker('block')} type="button" class={dateButtonClass}>Data di nascita:  {patient.birthdate.toString()}</button>
                        <DayPicker
                            captionLayout="dropdown"
                            fromYear={new Date().getFullYear() - 100}
                            toYear={new Date().getFullYear()}
                            mode="single"
                            selected={format(new Date(), 'y-MM-dd')}
                            onSelect={modifyPatientBirthdate}
                            showOutsideDays
                            style={{ display: showDatePicker }}
                        />
                    </div>
                    <div >
                        <FormControl fullWidth>
                            <InputLabel id="demo-simple-select-label">Genere</InputLabel>
                            <Select
                                labelId="demo-simple-select-label"
                                id="demo-simple-select"
                                value={patient.gender}
                                label="Sesso"
                                onChange={handleGender}>
                                <MenuItem value='M'>Maschio</MenuItem>
                                <MenuItem value='F'>Femmina</MenuItem>
                            </Select>
                        </FormControl>
                    </div>
                    <div >
                        <button onClick={() => handleDisplayProthesis()} type="button" class={protButtonClass}>Protesi <img src={question} alt="question mark" width={'10%'} /></button>
                    </div>
                </div>

                <div style={style.protLeft} >
                    <div style={{ display: showProthesis }}>
                        <label >
                            <Checkbox
                                sx={{ '& .MuiSvgIcon-root': { fontSize: 40 } }}
                                name="Gom-dx"
                                onChange={handleProthesis}
                            />Gom dx</label>
                    </div>
                    <div style={{ display: showProthesis }} >
                        <label >
                            <Checkbox
                                sx={{ '& .MuiSvgIcon-root': { fontSize: 40 } }}
                                name="Gin-dx"
                                onChange={handleProthesis}
                            />Gin dx</label>
                    </div>
                    <div style={{ display: showProthesis }}>
                        <label >
                            <Checkbox
                                sx={{ '& .MuiSvgIcon-root': { fontSize: 40 } }}
                                name="Cav-dx"
                                onChange={handleProthesis}
                            />Cav dx</label>
                    </div>
                </div>

                <div style={{ width: '33%' }}>
                    <img src={male} alt="male human silhouette" style={{ width: '75%', position: 'relative', margin: 'auto' }} />
                </div>
                <div style={style.prot}>

                    <div style={{ display: showProthesis }}>
                        <label >
                            <Checkbox
                                sx={{ '& .MuiSvgIcon-root': { fontSize: 40 } }}
                                name="Gom-sx"
                                onChange={handleProthesis}
                            />Gom sx</label>
                    </div>
                    <div style={{ display: showProthesis }}>
                        <label >
                            <Checkbox
                                sx={{ '& .MuiSvgIcon-root': { fontSize: 40 } }}
                                name="Gin-sx"
                                onChange={handleProthesis}
                            />Gin sx</label>
                    </div>
                    <div style={{ display: showProthesis }}>
                        <label >
                            <Checkbox
                                sx={{ '& .MuiSvgIcon-root': { fontSize: 40 } }}
                                name="Cav-sx"
                                onChange={handleProthesis}
                            />Cav sx</label>
                    </div>
                </div>

                <div style={{ width: '25%' }} >
                    <Slider name="height"
                        disabled={false}
                        marks={marksH}
                        max={220}
                        value={patient.height}
                        min={120}
                        orientation="vertical"
                        onChange={modifyPatientDimensions}
                        valueLabelDisplay="on"
                    />
                </div>

            </div>
            <div style={{ width: '65%' }} >
                <Slider name="weight"
                    disabled={false}
                    marks={marksW}
                    max={150}
                    value={patient.weight}
                    min={40}
                    onChange={modifyPatientDimensions}
                    valueLabelDisplay="on"
                />
            </div>

            <div style={style.forwardButton}>
                <button class="btn btn-success btn-lg" onClick={() => setShowModal(true)} >Aggiungi paziente</button>
            </div>

            <NewPatientModal data={{ navigate: navigate, showAlert: showAlert, patient: patient, disabled: disabled, showModal: showModal, handleNew: handleNew, sendingButton: sendingButton, setShowModal: setShowModal }} />

        </div>
    )
}

const style = {
    leftButtons: {
        width: '25%',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'space-between',
        marginBottom: '10%',
        marginLeft: '3.5%'
    },
    prot: {
        width: '3%',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'space-between',
        marginTop: '17%',
        marginRight: 'auto',

    },
    protLeft: {
        width: '3%',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'space-between',
        marginTop: '17%',
        marginLeft: 'auto',

    },
    box: {
        justifyContent: 'space-around',
        gap: 20,
        width: '90%',
        height: '90%',
        borderRadius: '15px',
        background: 'white',
        margin: 'auto',
        marginTop: '2%',
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center'
    },
    forwardButton: {
        justifyContent: 'right',
        display: 'flex',
        marginRight: '1%',
        marginBottom: '1%',
        marginLeft: 'auto',
        alignItems: 'right'
    }
}
